.indexPageContent{
    position: relative;
    top: 15vh;
    width: 70%;
    min-height: 100vh;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20%;
}

.heading{
    position: absolute;
    top: 5px;
    left: 50px;
    color: rgb(229, 224, 209);
    font-size: 1.5rem;
    font-style: italic;
}

.button {
    background-color: white;
    height: 100px;
    width: 100px;
}


.featured {
    cursor: pointer;
    user-select: none;
    position: relative;
    top: 80px;
    height:200px;
    width: 100%;
    background-color: #3c3c3c;
    border-radius: 10px;
    padding: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
}




.featuredPostTitle{
    width: fit-content;
    margin-left: 0px;
    margin-bottom: 30px;
    font-size: 1.8rem;
    color: rgb(150, 228, 232);
    font-family: "Helvetica Neue", Arial, sans-serif;
}

.featuredPostContent{
    color: #b4b3b3;
}